import uniqid from 'uniqid';
import { getRandomInt , clamp } from './utils';
import { gameRoundTime } from './constants';
export enum ContestState{
    open, 
    live,
    close
}

export class Contest{

    gameId      : string
    openTime    : Date
    closeTime   : Date
    liveTime    : number   
    duration    : number
    oddsUp      : number
    oddsDown    : number
    price       : number
    lockedPrice : number
    contestState : ContestState
    poolSize     : number;

    constructor(openTime : Date , state : ContestState , duration : number){

        this.gameId = uniqid();
        this.contestState = state;
        this.openTime = openTime;
        this.closeTime = new Date(openTime.getTime() + gameRoundTime * 1000);
        this.liveTime = 0;
        this.oddsUp = 0;
        this.oddsDown = 0;
        this.price = 0;
        this.lockedPrice = 0;
        this.poolSize = 0;
        this.duration = duration;
    }  
}

export class GameController {
    updateInterval : number = 250;
    interval = 1000;
    btValue = 0;
    baseOdds : number = 1.84;
    constests : Contest[] = [];

    constructor() {

        var socket = new WebSocket('wss://btc.data.hxro.io/live');
        socket.onmessage =  (event) => {
            const message = JSON.parse(event.data);
            this.btValue = message.price;
        };

        this.startContests();
        setInterval(()=>{
            this.updateGames();
        } , this.updateInterval);
    }

    startContests(){
        const date = new Date();
        this.constests[0] = new Contest(new Date(date.getTime() - gameRoundTime * 1000) , ContestState.close , gameRoundTime);
        this.constests[0].price = this.btValue;
        this.constests[0].liveTime = 60;

        this.constests[1] = new Contest(new Date(date.getTime()), ContestState.live , gameRoundTime);
        this.constests[1].lockedPrice = this.btValue;
        this.constests[2] = new Contest(new Date(date.getTime() +  gameRoundTime * 1000) , ContestState.open , gameRoundTime);
    }

    updateGames(){
        const date = new Date();
        const liveContest = this.constests[1];
        const currentTime : number = (date.getTime() - liveContest.openTime.getTime()) / this.interval;

        this.constests[1].liveTime =  clamp(currentTime, 0 , gameRoundTime);
        
        const len = this.constests.length;
        for(let i = 0 ; i <len;i++){

            switch(this.constests[i].contestState){
                case ContestState.close:
                    continue;
                case ContestState.open:
                    this.constests[i].poolSize += getRandomInt(50,100) / 10;
                    this.constests[i].poolSize = Math.round(this.constests[i].poolSize * 100) / 100;
                break;
                case ContestState.live:
                    if(this.constests[i].lockedPrice === 0)
                        this.constests[i].lockedPrice = this.btValue;

                break;
                
            }
                
            this.constests[i].price = this.btValue;
            this.constests[i].oddsUp = Math.round((this.baseOdds + getRandomInt(0,5) / 10) * 100) / 100;
            this.constests[i].oddsDown = Math.round((this.baseOdds - getRandomInt(0,5) / 10) * 100) / 100;
            
        }

        if(currentTime >= liveContest.duration)
            this.closeGames();

    }

    closeGames(){

        this.constests[1].contestState = ContestState.close;
        this.constests[2].contestState = ContestState.live;
        this.constests[2].lockedPrice = this.constests[2].price;

        this.constests[0] = this.constests[1];
        this.constests[1] = this.constests[2];
        this.constests[2] = this.createNewContest();
    }

    createNewContest() : Contest {

        const date = new Date();
        return new Contest(new Date(date.getTime() + gameRoundTime * 1000) , ContestState.open , gameRoundTime);
    }

    getContests() : Contest[] {
        
        return this.constests;
    }

}

const gameController = new GameController();
export const getContests = ()=>{

    return new Promise<Contest[]>(async(resolve , reject)=>{
        resolve(gameController.getContests());
    })

}
