import { BetType } from "./Enums";

export enum BetState{
    open,
    live,
    close
}

export default class GameBet {
    public gameId  :string;
    public stack : number;
    public betType : BetType;
    public state : BetState;
    public odds  :number;
    

    constructor(gameId:string , stack : number , betType : BetType , state:BetState , odds:number ) {

        this.gameId = gameId;
        this.stack = stack;
        this.betType = betType;
        this.state = state;
        this.odds = odds;
    }
}